.equipment-page {
	height: 100%;
	display: flex;
	flex-direction: column;

	.equipment-page-content {
		flex: 1 1 0;
		overflow-x: hidden;
		overflow-y: auto;
		-webkit-overflow-scrolling: touch;

		.card-selection-row {
			margin: 0 auto;
			width: 650px;
		}
	}

	.action {
		margin-bottom: 0;
	}
}
