.hero-builder-modal {
	display: flex;
	flex-direction: column;
	height: 100%;

	.header {
		border-bottom: 2px solid rgb(220, 220, 220);
		margin-bottom: 20px;
	}

	.content {
		flex: 1 1 0;
		overflow-x: hidden;
		overflow-y: auto;
		-webkit-overflow-scrolling: touch;

		hr {
			margin: 10px 0;
		}
	}
}
